import React from 'react' ;

const Navigation = ({onRoute, isSignedIn}) =>{
		if(isSignedIn){
		  return(	
			<nav style={{display: 'flex',justifyContent: 'flex-end'}}>
			  <p onClick = {() => onRoute('SignIn')}className='f3 link dim black underline pa3 pointer'> Sign Out </p>
			</nav>
		  )
		} else{
			return(
			 <nav style={{display: 'flex',justifyContent: 'flex-end'}}>
			   <p onClick = {() => onRoute('SignIn')}className='f3 link dim black underline pa3 pointer'> Sign In </p>
			   <p onClick = {() => onRoute('Register')}className='f3 link dim black underline pa3 pointer'> Register </p>
			</nav>
			)
		}

}

export default Navigation;


